.g-intr {
  width: 100vw;
  background: #93A7CB;
}
.g-intr .bg-wrap {
  background-origin: border-box;
  background-clip: border-box;
}
.g-intr .bg-wrap .logo-content {
  height: 25vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.g-intr .bg-wrap .logo-content .divide {
  display: block;
  height: 64px;
  border-right: 3px solid #ffffff;
  margin: 0 16px;
}
.g-intr .bg-wrap .logo-content .dd-logo {
  width: 56px;
}
.g-intr .bg-wrap h3 {
  color: #ffffff;
  font-family: Helvetica, sans-serif;
  font-size: 30px;
  margin: 0;
}
.g-intr .intr-wrap {
  display: flex;
  flex-direction: column;
  padding-top: 16px;
  align-items: center;
}
.g-intr .content-wrap {
  margin: 16px;
  padding: 12px;
  font-size: 14px;
  line-height: 24px;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.09);
}
.g-intr .content-wrap .about-us {
  text-indent: 24px;
}
.g-intr .content-wrap .left {
  text-align: left;
}
.g-intr .content-wrap .right {
  text-align: right;
}
.g-intr .content-wrap .center {
  text-align: center;
}
.g-intr .content-wrap .reason-text {
  margin-bottom: 16px;
}
.g-intr .content-wrap p {
  margin: 8px 0;
  line-height: 30px;
  font-size: 16px;
  font-weight: 400;
  text-indent: 30px;
}
.g-intr .content-wrap img {
  max-width: 90%;
  height: auto;
}
.g-intr .content-wrap h3 {
  font-family: Helvetica, sans-serif;
  text-align: center;
}
.g-intr .reason-warp {
  display: flex;
  flex-direction: column;
  padding-top: 16px;
  align-items: center;
}
.g-intr .reason-warp .reason-desc p {
  text-align: center;
  margin: 3px;
}
.g-intr .holder {
  height: 56px;
}
.g-intr .title {
  margin-bottom: 6px;
}
.g-intr .title h3 {
  font-family: Helvetica, sans-serif;
  margin: 3px;
}
.g-intr .title .divide-row {
  width: 100%;
  display: block;
  border-top: 2px solid #ffffff;
}
@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
.g-intr > .bg-wrap > .logo-content {
  -webkit-animation: fadeInUp 1000ms both;
          animation: fadeInUp 1000ms both;
}
.g-content > .g-intr > .intr-wrap,
.g-content > .g-intr > .reason-warp {
  -webkit-animation: fadeInUp 1250ms both;
          animation: fadeInUp 1250ms both;
  -webkit-animation-delay: 50ms;
          animation-delay: 50ms;
}
