.g-brad {
  display: flex;
  flex-direction: column;
  padding: 10px 10px 56px 10px;
  background: #fff;
}
.g-brad .m-ele-item {
  display: flex;
  margin: 0 0 10px 0;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
}
.g-brad .m-img {
  width: 40%;
  height: 100px;
}
.g-brad .m-img > img {
  width: 100%;
  height: 100%;
}
.g-brad .m-name {
  font-size: 14px;
  padding: 5px 10px;
}
.g-brad .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  justify-content: space-between;
}
.g-brad .m-info .m-btn-wrap {
  text-align: right;
}
.g-brad .m-info .m-btn-wrap .btn {
  margin: 3px 5px;
  height: 30px;
  padding: 3px 5px;
  width: 100px;
  outline: none;
}
.g-brad .m-area,
.g-brad .m-detail {
  flex: 1 1;
  padding: 0 10px;
  display: flex;
}
.g-brad .m-detail {
  justify-content: flex-end;
}
.g-brad .m-area > span {
  color: #cc0000;
  font-size: 12px;
}
.g-brad .m-btn {
  background: #eee;
  color: #666;
  height: 30px;
  padding: 3px 5px;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.g-brad .m-detail-wrap {
  display: flex;
  flex-direction: row;
  margin: 0 0 10px 0;
  padding: 10px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
}
.g-brad .m-detail-wrap .info {
  display: flex;
  flex: 3 1;
  flex-direction: column;
}
.g-brad .m-detail-wrap .info .title {
  font-size: 16px;
  font-family: Helvetica, sans-serif;
  margin-bottom: 20px;
}
.g-brad .m-detail-wrap .info .content {
  margin-bottom: 10px;
}
.g-brad .m-detail-wrap .info .content h3 {
  display: inline;
  margin-right: 6px;
}
.g-brad .m-detail-wrap .info .content .icon-copy {
  margin-left: 6px;
  color: #ededed;
}
.g-brad .m-detail-wrap .info .content .li {
  font-size: 12px;
}
.g-brad .m-detail-wrap .icon-wrap {
  flex: 1 1;
}
.g-brad .m-detail-wrap .icon-wrap img {
  width: 100%;
  height: auto;
}
