.g-coop {
  background: #fff;
  padding: 10px 20px 56px 20px;
}
.g-coop .m-coop {
  background-color: white;
  padding: 10px;
  margin-bottom: 10px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.05), -1px -1px 1px rgba(0, 0, 0, 0.05);
  border: 1px solid #eee;
}
.g-coop .m-coop-tl {
  font-size: 20px;
  font-weight: 600;
  color: #3366aa;
  margin-bottom: 10px;
  text-align: center;
}
.g-coop .ant-select-selection--single {
  height: 40px;
}
.g-coop .ant-select-selection__rendered {
  line-height: 40px;
}
