.g-examcom {
  display: flex;
  flex-direction: column;
  padding: 10px 10px 56px 10px;
}
.g-examcom .m-layout-wrap {
  margin-bottom: 10px;
}
.g-examcom .m-btn {
  background: #eee;
  color: #666;
  height: 30px;
  padding: 3px 5px;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.g-examcom .m-img {
  width: 40%;
  height: 100px;
}
.g-examcom .m-img > img {
  width: 100%;
  height: 100%;
}
.g-examcom .m-name {
  font-size: 14px;
  padding: 5px;
}
.g-examcom .m-intr {
  padding: 0 5px;
}
.g-examcom .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-examcom .m-info {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.g-examcom .m-area {
  flex: 1 1;
  padding: 0 10px 10px 0;
  align-items: flex-end;
  display: flex;
  justify-content: flex-end;
}
.g-examcom .m-area > span {
  background: #0066aa;
  padding: 2px 5px;
  color: #fff;
}
.g-examcom .shadow {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
}
.g-examcom .m-brief {
  display: flex;
}
.g-examcom .m-detail .m-name {
  text-align: center;
  background: #666;
  color: #fff;
}
.g-examcom .m-detail .m-img {
  width: 100%;
  height: auto;
}
.g-examcom .m-detail .m-detail-content {
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  margin: 0 0 10px 0;
}
.g-examcom .m-detail .m-detail-content > span {
  margin-bottom: 5px;
}
