.g-sche {
  background: #fff;
  padding: 10px 10px 56px 10px;
  min-height: 100%;
  position: relative;
}
.g-sche .m-item {
  font-size: 12px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.05), -2px -2px 2px rgba(0, 0, 0, 0.05);
  margin: 10px 0;
  padding: 5px;
}
.g-sche .m-addr {
  flex: 1 1;
  font-size: 16px;
  padding: 5px;
}
.g-sche .m-info > div {
  padding: 5px;
}
.g-sche .m-info > div > span {
  margin-right: 5px;
}
.g-sche .m-proc {
  padding: 5px 0;
}
.g-sche .m-fun {
  margin-top: 5px;
  border-top: 1px solid #eeee;
  padding: 10px 5px 5px 5px;
  text-align: right;
}
.g-sche .m-proc-item {
  display: flex;
}
.g-sche .m-info {
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
}
.g-sche .m-proc-stat {
  flex: unset!important;
}
.g-sche .m-proc-dt {
  width: 135px;
}
.g-sche .m-proc-ct {
  flex: 1 1;
}
.g-sche .m-proc-stat {
  width: 50px;
  text-align: center;
}
.g-sche .m-comment {
  margin-bottom: 10px;
}
